import { Component } from 'react'

import Child from "./child"

export default class childtoparent extends Component {
    constructor(props) {
        super(props);
        this.state = { name: "" }
    }

    // thisundefined
    // sonChange(name) {
    //     this.setState({ name: name })
    // }
    // thisthis
    sonChange = name => {
        this.setState({ name: name })
    }

    render() {
        return (
            <Child name={this.state.name} parent={this} />
        )
    }
}